var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_vm._ssrNode("<section class=\"inner-section\" data-v-d1f2c15a><article class=\"column-section column-section__1\" data-v-d1f2c15a><h2 data-v-d1f2c15a>MANIFESTO</h2> <h2 data-v-d1f2c15a>1: COLLAB MOTIVATION MATRIX</h2> <p data-v-d1f2c15a>\n        The goal of collaboration is to create a sum greater than the whole of\n        its parts: brands collaborate out of a utilitarian urge to pool their\n        collective audience reach. Luxury brands want to downmarket their\n        brand (without diluting it) and so collaborate with sportswear\n        companies; streetwear brands want the approval of fashion even as they\n        position themselves outside its norms and so they collaborate with\n        high end designers. This is extremely effective.\n      </p> <img"+(_vm._ssrAttr("src",require("assets/img/collab-matrix.png")))+" alt data-v-d1f2c15a></article> <article class=\"column-section column-section__2\" data-v-d1f2c15a><p data-v-d1f2c15a>\n        For the purposes of this discussion we will define “collaboration”\n        broadly, and include all production relationships in which two (or\n        more) brands are recognizable by a consumer at the point of retail.\n        The utility of a given collaboration is determined by the relative\n        cultural status of the participants, and whether the collaboration is\n        consensual.\n      </p> <h2 data-v-d1f2c15a>THE WILLING REGION</h2> <h3 data-v-d1f2c15a>New X New: Friends</h3> <p data-v-d1f2c15a>\n        “Let’s both work together because we like and/or respect each other’s\n        work and are both on the up and up.” We submit that this relationship\n        is essentially never present in a consumer product.\n      </p> <h3 data-v-d1f2c15a>New X Old: Clout Chasing</h3> <p data-v-d1f2c15a>\n        New seeks to cosign with Old to acquire the credibility of\n        establishment in a space, or institutional approval\n      </p> <h3 data-v-d1f2c15a>Old X New: Relevance Chasing</h3> <p data-v-d1f2c15a>\n        Old seeks to associate with New to demonstrate that there is still\n        breath in the old dead body\n      </p> <h3 data-v-d1f2c15a>Old X Old: Let’s Get This Bread</h3> <p data-v-d1f2c15a>\n        The relationship between these two may make no semantic sense, but\n        both are so established it doesn’t matter. True profit-seeking\n        symbiosis.\n      </p></article> <article class=\"column-section column-section__3\" data-v-d1f2c15a><h2 data-v-d1f2c15a>THE UNWILLING REGION</h2> <h3 data-v-d1f2c15a>New X New, Unwilling: Knockoff</h3> <p data-v-d1f2c15a>\n        A new or little-known brand copies from another new or little-known\n        brand\n      </p> <h3 data-v-d1f2c15a>New X Old, Unwilling: Heist</h3> <p data-v-d1f2c15a>\n        New steals from Old, gaining clout without acceding to any demands in\n        return. Fight The Man.\n      </p> <h3 data-v-d1f2c15a>Old X New, Unwilling: IP Theft</h3> <p data-v-d1f2c15a>\n        Old copies New’s designs and reproduces them for a mass audience New\n        cannot reach.\n      </p> <h3 data-v-d1f2c15a>Old X Old, Unwilling: Purchase</h3> <p data-v-d1f2c15a>\n        Old buys the rights to another Old’s product, or buys the company\n        outright, taking over their brand and using it for their own gain\n        without regard to its longevity.\n      </p></article> <article class=\"column-section column-section__4\" data-v-d1f2c15a><h2 data-v-d1f2c15a>2. COLLAB CONTINUUM</h2> <p data-v-d1f2c15a>\n        Supreme X Louis Vuitton<br data-v-d1f2c15a>\n        Versace X H&amp;M<br data-v-d1f2c15a>\n        Vetements X DHL<br data-v-d1f2c15a>\n        ...Le Creuset X Star Wars???\n      </p> <p data-v-d1f2c15a>\n        The ubiquity of collaborative paradigms gradually moves us away from a\n        world in which they matter. Meaningful brand collaborations rely on\n        each component part being distinct, and the combinative result\n        therefore a surprise, a study in contrasts, or pairing of\n        complementary flavors. When collaboration becomes the rule of an\n        industry, rather than the exception - when brands cross over like the\n        fumbling, arbitrary results of sex dice - they serve to smooth the\n        entire retail landscape into an even gradient.\n      </p> <p data-v-d1f2c15a>\n        Nowhere is this more evident than in streetwear, the underground,\n        outcast, rebellious power users of print on demand t-shirt services,\n        each desperate to differentiate even as they are beholden to\n        lowest-common-denominator production practices.\n      </p> <p data-v-d1f2c15a>\n        Brands bandy about their logos like trading cards and after a certain\n        point they’re all simultaneously licensing to each other such that the\n      </p></article> <article class=\"column-section column-section__5\" data-v-d1f2c15a><img"+(_vm._ssrAttr("src",require("assets/img/collab-continuum.png")))+" alt data-v-d1f2c15a></article> <article class=\"column-section column-section__6\" data-v-d1f2c15a><p data-v-d1f2c15a>\n        chain of agreements goes circular. This ouroboros of usage rights\n        ultimately boils down to one de facto conclusion: streetwear is,\n        rather than a genre, a single uber-brand of functionally\n        interchangeable parts.\n      </p> <p data-v-d1f2c15a>\n        We term this the Collaborative Continuum: a distribution of brands and\n        audiences no longer discrete but continually linked through a mesh of\n        crossovers, blending seamlessly into one another through their history\n        of retail association. When you buy one, odds are good you’re getting\n        another on the side; not two brands, but one selected manifestation of\n        a single entity.\n      </p> <p data-v-d1f2c15a>\n        MSCHF X flattens the curation and purchase of two hands worth of\n        individual brands into a single shirt. Simultaneously, through some\n        applied hacksmanship, we remove these pieces from their pedestals -\n        none have unique worth beyond the simple associative value of their\n        logos.\n      </p> <p data-v-d1f2c15a>\n        What remains, when these decontextualized signifiers are stitched back\n        into a pastiche canvas, is the platonic ideal of 2020 streetwear in\n        its entirety. This is the last shirt you’ll ever need, and a snapshot\n        of a scene in time.\n      </p></article> <article class=\"column-section column-section__7\" data-v-d1f2c15a></article></section> <section class=\"inner-section-mobile\" data-v-d1f2c15a><article id=\"base-section\" class=\"column-section column-section__1\" data-v-d1f2c15a><h2 data-v-d1f2c15a>MANIFESTO</h2> <h2 data-v-d1f2c15a>1: COLLAB MOTIVATION MATRIX</h2> <p data-v-d1f2c15a>\n        The goal of collaboration is to create a sum greater than the whole of\n        its parts: brands collaborate out of a utilitarian urge to pool their\n        collective audience reach. Luxury brands want to downmarket their\n        brand (without diluting it) and so collaborate with sportswear\n        companies; streetwear brands want the approval of fashion even as they\n        position themselves outside its norms and so they collaborate with\n        high end designers. This is extremely effective.\n      </p> <img"+(_vm._ssrAttr("src",require("assets/img/collab-matrix.png")))+" alt data-v-d1f2c15a></article> <article class=\"column-section column-section__2\" data-v-d1f2c15a><p data-v-d1f2c15a>\n        For the purposes of this discussion we will define “collaboration”\n        broadly, and include all production relationships in which two (or\n        more) brands are recognizable by a consumer at the point of retail.\n        The utility of a given collaboration is determined by the relative\n        cultural status of the participants, and whether the collaboration 